@charset "utf-8";
*{
  padding: 0px;
  margin: 0px;
}
ul,ol,li{
  list-style: none;
}
body{
  background-color: #ccc;
}
.intro{
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.intro>img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  /* animation-name: intro;
  animation-iteration-count: unset;
  animation-duration: 5s; */
}
/* @keyframes intro{
  0%{
    transform: scale(150%);
  }

  100%{
    transform: scale(100%);
  }
} */
.contain{
  display: flex;
  width: 100%;
  justify-content: space-around;
  flex-wrap: wrap;
}
article{
  width: 44%;
  height: 260px;
  display: flex;
  align-items: center;
  background-color: #fff;
  margin: 3% auto;
  padding: 20px;
  position: relative;
  box-shadow: -9px 8px 15px rgba(0, 0, 0,.5);
}
article>img{
  width: 30%;
  position: absolute;
  top: -20px;
  box-shadow: -9px 8px 15px rgba(0, 0, 0,.5);
}
article div{
  margin-left: 38%;
}
article h3{
  font-size: 16px;
  margin-bottom: 5px;
}
article p{
  font-size: 14px;
}
article ul{
  margin-bottom: 5px;
  display: flex;
  color: #006aff;
  font-weight: 500;
}
article ul li:not(:last-child){
  margin-right: 10px;
}
article p:first-of-type{
  margin-bottom: 5px;
}